import { FC } from "react";
import { TFilm } from "../../types/TFilm";
import styles from "./_FilmInfoView.module.scss";
import { FilmInfoEl } from "./FilmInfoEl/FilmInfoEl";
import { Container } from "../Container/Container";

type Props = {
  data: TFilm;
};

export const FilmInfoView: FC<Props> = ({ data }) => {
  return (
    <div className={styles["about-film"]}>
      <Container>
        <h3 className={styles["about-film__title"]}> </h3>
        <ul className={styles["about-film__list"]}>
          <li>
            <FilmInfoEl
              field=" "
              text={data.originalTitle}
            />
          </li>
          <li>
            <FilmInfoEl field=" " text={data.language} />
          </li>
          {data.budget ? (
            <li>
              <FilmInfoEl field="" text={`${data.budget} $`} />
            </li>
          ) : null}
          {data.production ? (
            <li>
              <FilmInfoEl field="" text={data.production} />
            </li>
          ) : null}
          {data.awardsSummary ? (
            <li>
              <FilmInfoEl field="" text={data.awardsSummary} />
            </li>
          ) : null}
          {data.director ? (
            <li>
              <FilmInfoEl field="" text={data.director} />
            </li>
          ) : null}
        </ul>
      </Container>
    </div>
  );
};
